<template>
<div class="components-container">
  <h1>Markdown</h1>
  <p>
    这里用了：<a href="https://github.com/sparksuite/simplemde-markdown-editor" target="_blank">simplemde-markdown-editor</a> <br /> 简单的用vue封装了一下,如果需求方能接受 markdown 就一定要用 markdown，坑真心会比富文本少很多。<br /> 这里我们用 markdown 做了编辑器，还需要一个能解析的的东西，可以你传给后端让后端帮你转化，也可以前端自己来，推荐一个转化库：
    <a href="https://github.com/showdownjs/showdown">showdown</a>
  </p>
  <div class="editor-container">
    <md-editor id='contentEditor' ref="contentEditor" v-model='content' :height="300" :zIndex='20'></md-editor>
  </div>
  <el-button @click='markdown2Html' type="primary">转为HTML<i class="el-icon-document el-icon--right"></i></el-button>
  <div v-html="html"></div>
</div>
</template>

<script>
import MdEditor from '..//markdown/MdEditor';
export default {
  components: {
    MdEditor
  },
  data() {
    return {
      content: '## Simplemde',
      html: ''
    }
  },
  methods: {
    markdown2Html() {
      import ('showdown').then(showdown => {
        const converter = new showdown.Converter();
        this.html = converter.makeHtml(this.content)
      })
    }
  }
};
</script>
<style scoped>
.components-container {
  min-width: 560px;
  background-color: #e5e5e5;
  border-radius: 10px;
  line-height: 20px;
  padding: 10px;
}
</style>



