<template>
<div id="imgheader">
  <div class="photo">
    <a class="btn" @click="toggleShow">Uploader</a>
    <img id='newimg' :src='srctiele'>
    <img id='newimg2' :src='srctiele'>
  </div>
  <my-upload field="img" v-model="show" :width="300" :height="300" url="/upload" :params="params" :headers="headers" img-format="png">
  </my-upload>
  <div></div>
</div>
</template>

<script>
import Vue from 'vue';
import myUpload from 'vue-image-crop-upload';
import store from '@/vuex/store'
export default {
  name: 'page01',
  data() {
    return {
      show: false,
      params: {
        token: '123456798',
        name: 'avatar'
      },
      headers: {
        smail: '*_~'
      },
      imgDataUrl: ''
    }
  },
  components: {
    'my-upload': myUpload
  },
  computed: {
    srctiele() {
      return this.$store.state.src
    }
  },
  methods: {
    toggleShow() {
      this.show = !this.show;
    }
  },
  events: {
    cropSuccess(imgDataUrl, field) {
      console.log('-------- crop success --------');
      this.imgDataUrl = imgDataUrl;
    },
    cropUploadSuccess(jsonData, field) {
      console.log('-------- upload success --------');
      console.log(jsonData);
      console.log('field: ' + field);
    },
    cropUploadFail(status, field) {
      console.log('-------- upload fail --------');
      console.log(status);
      console.log('field: ' + field);
    }
  }
}
</script>
<style scoped>
#imgheader {
  min-width: 820px;
  padding-top: 20px;
  position: relative;
  background-color: #e5e5e5;
  border-radius: 10px;
  height: 600px;
}

#imgheader .btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  cursor: pointer;
  display: block;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  color: orange;
  width: 100px;
  text-align: center;
  background-color: #fff;
	box-shadow: #666 0px 0px 20px;
  border-radius: 5px;
}

#newimg {
  position: absolute;
  left: 10%;
  top: 40px;
  height: 300px;
  width: 300px;
  border: 5px solid #fff;
	box-shadow: #fff 0px 0px 5px;
}

#newimg2 {
  position: absolute;
  left: 50%;
  top: 40px;
  height: 300px;
  width: 300px;
  border-radius: 50%;
  border: 5px solid #fff;
	box-shadow: #fff 0px 0px 5px;
}

#imgheader .photo{
  width: 800px;
  height: 400px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 2px solid #fff;
  border-radius: 10px;
  background: #eef1f6;
	box-shadow: #fff 0px 0px 20px;
}

</style>



