<template>
<div id="tabledrag">
  <div class="fluid container">
    <div class="form-group form-group-lg panel panel-default">
      <div class="panel-body">
        <button type="button" class="btn btn-default" @click="orderList">初始化排序</button>
      </div>
    </div>

    <div class="col-md-3">
      <draggable class="list-group" element="ul" v-model="list" :options="dragOptions" :move="onMove" @start="isDragging=true" @end="isDragging=false">
        <transition-group type="transition" :name="'flip-list'">
          <li class="list-group-item" v-for="element in list" :key="element.order">
            id：{{element.order}}<i :class="element.fixed? 'fa fa-anchor' : 'glyphicon glyphicon-pushpin'" @click=" element.fixed=! element.fixed" aria-hidden="true" style="margin-left:100px"></i> {{element.name}}
          </li>
        </transition-group>
      </draggable>
    </div>
  </div>
</div>
</template>

<script>
import draggable from 'vuedraggable'
const message = [
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花',
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花',
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花',
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花',
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花',
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花',
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花',
  '活动情况：2017年12月25 - 上海市普陀区真北路 - 圣诞主题活动 - 寻找玫瑰花'
]
export default {
  name: 'tabledrag',
  components: {　　
    draggable
  },
  data() {
    return {
      list: message.map((name, index) => {
        return {
          name,
          order: index + 1,
          fixed: false
        };
      }),
      editable: true,
      isDragging: false,
      delayedDragging: false
    }
  },
  methods: {
    orderList() {
      this.list = this.list.sort((one, two) => {
        return one.order - two.order;
      })
    },
    onMove({
      relatedContext,
      draggedContext
    }) {
      const relatedElement = relatedContext.element;
      const draggedElement = draggedContext.element;
      return (!relatedElement || !relatedElement.fixed) && !draggedElement.fixed
    }
  },
  computed: {
    dragOptions() {
      return {
        animation: 0,
        group: 'description',
        disabled: !this.editable,
        ghostClass: 'ghost'
      };
    },
    listString() {
      return JSON.stringify(this.list, null, 2);
    },
    list2String() {
      return JSON.stringify(this.list2, null, 2);
    }
  },
  watch: {
    isDragging(newValue) {
      if (newValue) {
        this.delayedDragging = true
        return
      }
      this.$nextTick(() => {
        this.delayedDragging = false
      })
    }
  }
}
</script>
<style scoped>
#tabledrag {
  border-radius: 10px;
  width: 100%;
  min-width: 500px;
  min-height: 540px;
  padding-bottom: 30px;
  padding-top: 30px;
  background: #e5e5e5;
}

#tabledrag .flip-list-move {
  transition: transform 0.5s;
}

#tabledrag .panel-body .btn-default {
  font-size: 16px;
  font-weight: 900;
  background: #fff;
  color: #000;
  box-shadow: 3px 3px 3px #ccc;
  border-radius: 10px;
  padding-left: 20px;
  padding-right: 20px;
}

#tabledrag .panel-body {
  margin-left: 50px;
}

#tabledrag .col-md-3 {
  margin-left: 10px;
  margin-right: 10px;
}

#tabledrag .col-md-3 li {
  background: #fff;
  line-height: 40px;
  border: 1px solid #000;
  width: 95%;
  border-radius: 5px;
  margin: 20px;
  text-indent: 30px;
  cursor: move;
  list-style: none;
}

#tabledrag .ghost {
  opacity: .5;
  background: #C8EBFB;
}

#tabledrag .list-group {
  padding: 0;
  margin-left: 0px;
}
</style>



