<template>
<div id="excel">
  <div>
    <el-button type="primary" @click="export2Excel" class='excelbtn'>导出Excel</el-button>
  </div>
  <el-table id="tableExcel" :data="tableData" border style="width: 100%">
    <el-table-column prop="date" label="日期" sortable width="180">
    </el-table-column>
    <el-table-column prop="name" label="姓名" width="180">
    </el-table-column>
    <el-table-column prop="address" label="地址" :formatter="formatter">
    </el-table-column>
    <el-table-column prop="tag" label="标签" width="100" :filters="[{ text: '家', value: '家' }, { text: '公司', value: '公司' }]" :filter-method="filterTag" filter-placement="bottom-end">
      <template scope="scope">
                <el-tag
                :type="scope.row.tag === '家' ? 'primary' : 'success'"
                close-transition>{{scope.row.tag}}</el-tag>
            </template>
    </el-table-column>
  </el-table>
</div>
</template>

<script>
import { method5 } from './excel.js'

export default {
  data() {
    return {
      tableData: [{
        ID: "1",
        date: '2017-05-02',
        name: '王小仙1',
        address: '上海市普陀区金沙江路 1518 弄',
        tag: '家'
      }, {
        ID: "2",
        date: '2017-05-04',
        name: '王小仙2',
        address: '上海市普陀区金沙江路 1517 弄',
        tag: '公司'
      }, {
        ID: "3",
        date: '2017-05-01',
        name: '王小仙3',
        address: '上海市普陀区金沙江路 1519 弄',
        tag: '家'
      }, {
        ID: "4",
        date: '2017-05-03',
        name: '王小仙4',
        address: '上海市普陀区金沙江路 1516 弄',
        tag: '公司'
      }, {
        ID: "5",
        date: '2017-05-03',
        name: '王小仙5',
        address: '上海市普陀区金沙江路 1516 弄',
        tag: '家'
      }, {
        ID: "6",
        date: '2017-05-03',
        name: '王小仙6',
        address: '上海市普陀区金沙江路 1516 弄',
        tag: '公司'
      }, {
        ID: "7",
        date: '2017-05-03',
        name: '王小仙7',
        address: '上海市普陀区金沙江路 1516 弄',
        tag: '家'
      }, {
        ID: "8",
        date: '2017-05-03',
        name: '王小仙8',
        address: '上海市普陀区金沙江路 1516 弄',
        tag: '公司'
      }, {
        ID: "9",
        date: '2017-05-03',
        name: '王小仙9',
        address: '上海市普陀区金沙江路 1516 弄',
        tag: '家'
      }, {
        ID: "10",
