<template>
<div class="app-container">
  <h1>动态换肤</h1>
  <el-card class="box-card">
    <div slot="header">
      <span style="line-height: 36px;">偏好设置</span>
    </div>

    <div class="box-item">
      <span class="field-label">换肤:</span>
      <el-switch v-model="theme" on-text="" off-text="">
      </el-switch>
    </div>
  </el-card>

  <div class="block">
    <span class="demonstration">Button: </span>
    <span class="wrapper">
        <el-button type="success">成功按钮</el-button>
        <el-button type="warning">警告按钮</el-button>
        <el-button type="danger">危险按钮</el-button>
        <el-button type="info">信息按钮</el-button>
       </span>
  </div>

  <div class="block">
    <el-tag class='tag-item' v-for="tag in tags" :type="tag.type" :key='tag.type'>
      {{tag.name}}
    </el-tag>
  </div>

  <div class="block">
    <el-alert class='alert-item' title="成功提示的文案" type="success">
    </el-alert>
    <el-alert class='alert-item' title="消息提示的文案" type="info">
    </el-alert>
    <el-alert class='alert-item' title="警告提示的文案" type="warning">
    </el-alert>
    <el-alert class='alert-item' title="错误提示的文案" type="error">
    </el-alert>
  </div>

</div>
</template>


<script>
import {
  toggleClass
} from '../../utils';
import '../../assets/custom-theme/index.css'

export default {
  data() {
    return {
      theme: false,
      tags: [{
          name: '标签一',
          type: ''
        },
        {
          name: '标签二',
          type: 'gray'
        },
        {
          name: '标签三',
          type: 'primary'
        },
        {
          name: '标签四',
          type: 'success'
        },
        {
          name: '标签五',
          type: 'warning'
        },
        {
          name: '标签六',
          type: 'danger'
        }
      ],
      inputVisible: false,
      inputValue: ''
    }
  },
  watch: {
    theme() {
      toggleClass(document.body, 'custom-theme')
      //   this.$store.dispatch('setTheme', value);
    }
  }
};
</script>

<style scoped>
.app-container {
  min-width: 550px;
}

.app-container h1 {
  text-indent: 50px;
  background-color: #e4e8f1;
  border-radius: 10px;
  margin: 10px;
}

.app-container .box-card {
  width: 400px;
  margin: 20px auto;
}

.app-container .block {
  padding: 30px 24px;
}

.app-container .alert-item {
  margin-bottom: 10px;
}

.app-container .tag-item {
  margin-right: 15px;
}
</style>



