<template>
<div class="login-container">
  <el-form autoComplete="on" :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left" label-width="0px" class="card-box login-form">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="email">
      <span class="svg-container"></span>
      <el-input name="email" type="text" v-model="loginForm.email" autoComplete="on" placeholder="邮箱"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <span class="svg-container"></span>
      <el-input name="password" type="password" @keyup.enter.native="handleLogin" v-model="loginForm.password" autoComplete="on" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleLogin">
        登录
      </el-button>
    </el-form-item>
    <div class='tips'>admin账号为:admin@www.com 密码为任意的6位数字</div>
    <div class='tips'>editor账号为:editor@www.com 密码为任意的6位数字</div>
  </el-form>

</div>
</template>

<script>
import { isWscnEmail } from '../../utils/validate';

export default {
  name: 'login',
  data() {
    const validateEmail = (rule, value, callback) => {
      if (!isWscnEmail(value)) {
        callback(new Error('请输入正确的合法邮箱'));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error('密码不能小于6位'));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        email: 'admin@www.com',
        password: ''
      },
      loginRules: {
        email: [{
          required: true,
          trigger: 'blur',
          validator: validateEmail
        }],
        password: [{
          required: true,
          trigger: 'blur',
          validator: validatePass
        }]
      },
      loading: false,
    }
  },
  methods: {
    handleLogin() {
      var _this = this;
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          let arr = _this.$store.state.loginmessage;
          arr.forEach((e, i) => {
            if (e.passname == this.loginForm.email) {

              this.error = ''
              this.$store.state.logintrue = e;
              let login = {
                name: e.passname,
                password: e.password,
                login: e.login
              }
              localStorage.setItem('LoginByEmail', JSON.stringify(login))
              this.$router.push({
                path: '/'
              })
            } else {
              alert('请输入正确的邮箱');
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    }
  }
}
</script>

<style>
.tips {
  font-size: 14px;
  color: #fff;
  margin-bottom: 5px;
  text-align: center;
}

.login-container {
  @include relative;
  z-index: 10000;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #2d3a4b;
}

.login-container .login-form {
  width: 500px;
}

.login-container input {
  background: transparent;
  border: 0px;
  border-radius: 0px;
  padding: 12px 5px 12px 15px;
  color: #eeeeee;
  height: 47px;
}

.login-container .el-input {
  display: inline-block;
  height: 47px;
  width: 85%;
}

.login-container .svg-container {
  padding: 6px 5px 6px 15px;
  color: #889aa4;
}

.login-container .title {
  font-size: 26px;
  font-weight: 400;
  color: #eeeeee;
  margin: 0px auto 40px auto;
  text-align: center;
  font-weight: bold;
}

.login-container .login-form {
  position: absolute;
  left: 0;
  right: 0;
  width: 400px;
  padding: 35px 35px 15px 35px;
  margin: 120px auto;
}

.login-container .el-form-item {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  color: #454545;
}

.login-container .forget-pwd {
  color: #fff;
}
</style>



