<template>
<div class="components-container" style="margin-top:-20px;min-width:540px;">
  <div class="info">
    <a href="http://ueditor.baidu.com/doc/">UE编辑器</a><br /> 需要使用编辑器时，调用UE公共组件即可。可设置填充内容defaultMsg，配置信息config(宽度和高度等)，可调用组件中获取内容的方法。支持页面内多次调用。
  </div>
  <button @click="getUEContent()" style="margin-top:20px;margin-bottom:10px">获取内容</button>
  <div class="editor-container">
    <UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue" style="min-width: 500px;"></UE>
    <UE :defaultMsg=defaultMsg :config=config :id=ue2 ref="ue2" style="min-width: 500px;"></UE>
  </div>
</div>
</template>
<style scoped>
.info {
  min-width: 500px;
  padding: 20px;
  background-color: #e5e5e5;
}
</style>
<script>
import UE from '../editor/ue/ue.vue';
export default {
  components: {
    UE
  },
  data() {
    return {
      defaultMsg: '这里是UE测试',
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      ue1: "ue1", // 不同编辑器必须不同的id
      ue2: "ue2"
    }
  },
  methods: {
    getUEContent() {
      let content = this.$refs.ue.getUEContent(); // 调用子组件方法
      this.$notify({
        title: '获取成功，可在控制台查看！',
        message: content,
        type: 'success'
      });
      console.log(content)
    }
  }
};
</script>



