<template>
<!--为echarts准备一个具备大小的容器dom-->
<div id="pieChart" style="height: 600px;min-width:500px"></div>
</template>
<script>
// 导入echarts
import echarts from 'echarts'
// 方便AJAX，按个人喜好添加，然后对应修改下方获取数据的代码
import $ from 'jquery'
import { resize } from 'jquery'


// 以饼图为例
// 其他种类图表配置项参见百度echarts官网


export default {
  data() {
    return {
      // 初始化空对象
      chart: null,
      // 初始化图表配置
      opinion: ['Android', 'iOS', 'Windows', 'Mac', 'Linux'],
      opinionData: [{
        value: 26,
        name: 'Android'
      }, {
        value: 31,
        name: 'iOS'
      }, {
        value: 18,
        name: 'Windows'
      }, {
        value: 28,
        name: 'Mac'
      }, {
        value: 21,
        name: 'Linux'
      }]
    }
  },
  methods: {
    // 绘图
    drawGraph(id) {
      // 绘图方法
      this.chart = echarts.init(document.getElementById(id))
      // 皮肤添加同一般使用方式
      this.chart.showLoading()
      // 返回到data中
      var that = this;
      window.addEventListener('resize',function () {
        that.chart.resize();
      })
      // set
      this.chart.setOption({
        // backgroundColor:"rgb(8, 38, 58)",
        backgroundColor:"#e5e5e5",
        title: {
          text: '打开终端占比统计--2016年',
          subtext: '虚拟数据',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          x: 'center',
          y: 'bottom',
          data: this.opinion // 别忘了this
        },
        toolbox: {
          show: true,
          feature: {
            mark: {
              show: true
            },
            dataView: {
              show: true,
              readOnly: false
            },
            magicType: {
              show: true,
              type: ['pie']
            },
            restore: {
              show: true
            },
            saveAsImage: {
              show: true
            }
          }
        },
        calculable: true,
        series: [{
          name: '访问来源',
          type: 'pie',
          // 内圆半径，外圆半径
          radius: [30, 100],
          // 位置，左右，上下
          center: ['50%', '50%'],
          roseType: 'area',
          data: this.opinionData, // 别忘了this
        }]
      })
      this.chart.hideLoading()
    }
  },
  // keypoint：执行方法
  // “将回调延迟到下次 DOM 更新循环之后执行。在修改数据之后立即使用它，然后等待 DOM 更新。”
  mounted() {
    this.$nextTick(function() {
      this.drawGraph('pieChart')
    })
  }
}
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
</style>



