import Vue from 'vue'
import Router from 'vue-router'
//登录
import login from '@/components/login/login'
//主页
import Hello from '@/components/hello/Hello'
import introduction from '@/components/introduction/introduction'
//表格
import desc_chart from '@/components/echarts/desc'
import barChart from '@/components/echarts/barChart'
import keyboard from '@/components/echarts/keyboard'
import lineChart from '@/components/echarts/lineChart'
import pieChart from '@/components/echarts/pieChart'
//组件
import desc_component from '@/components/component/desc/desc'
import editor from '@/components/component/editor/editor'
import markdown from '@/components/component/markdown/markdown'
import uploadHead from '@/components/component/uploadHead/uploadHead'
import todolist from '@/components/component/todolist/todolist'
//表格
import table from '@/components/table/table/table'
import tabledrag from '@/components/table/tabledrag/tabledrag'
import tabledit from '@/components/table/tabledit/tabledit'
import theme from '../components/theme/theme.vue'
//导出Excel
import excel from '@/components/excel/excel.vue'
//活动
import activePublic from '@/components/active/activePublic/activePublic'
import step1 from '@/components/active/activePublic/step1.vue'
import step2 from '@/components/active/activePublic/step2.vue'
import step3 from '@/components/active/activePublic/step3.vue'
import step4 from '@/components/active/activePublic/step4.vue'
import activeManage from '@/components/active/activeManage/activeManage'
import page from '@/components/active/activeManage/page'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: '登录',
      component: login
    }, {
      path: '/',
      name: '主页',
      component: Hello
    }, {
      path: '/introduction',
      name: '项目概述',
      component: introduction
    },
    //图表
    {
      path: '/charts/desc',
      name: '介绍',
      component: desc_chart
    }, {
      path: '/charts/barChart',
      name: '柱状图表',
      component: barChart
    }, {
      path: '/charts/keyboard',
      name: '琴键图表',
      component: keyboard
    }, {
      path: '/charts/lineChart',
      name: '折线图表',
      component: lineChart
    }, {
      path: '/charts/pieChart',
      name: '饼状图表',
      component: pieChart
    },
    //组件
    {
      path: '/component/desc',
      name: '描述',
      component: desc_component
    }, {
      path: '/component/editor',
      name: '富文本编辑器',
      component: editor
    }, {
      path: '/component/markdown',
      name: 'markdown编辑器',
      component: markdown
    }, {
      path: '/component/uploadHead',
      name: '头像上传',
      component: uploadHead
    }, {
      path: '/component/todolist',
      name: 'todolist',
      component: todolist
    },
    //导出Excel
    {
      path: '/excel',
      name: '导出Excel',
      component: excel
    },
    //Table
    {
      path: '/table/table',
      name: 'table',
      component: table
    }, {
      path: '/table/tabledrag',
      name: '表格拖拽',
      component: tabledrag
    }, {
      path: '/table/tabledit',
      name: '表格内编辑',
      component: tabledit
    },
    //换肤
    {
      path: '/theme',
      name: '换肤',
      component: theme
    },
    //活动发布
    {
      path: '/active/activePublic',
      component: activePublic,
      children: [
        {
          path: '',
          component: step1,
          name: '活动发布'
        }, {
          path: 'step2',
          component: step2,
          name: '报名签到'
        }, {
          path: 'step3',
          component: step3,
          name: '分享设置'
        }, {
          path: 'step4',
          component: step4,
          name: '个性设置'
        }
      ]
    },
    //活动内容
    {
      path: '/active/activeManage',
      name: '活动管理',
      component: activeManage
    }, {
      path: '/active/page',
      name: '活动管理',
      component: page
    }
  ]
})



