<template>
<div id="exTable">
  <form action="" name="form" id="form">
    <span>
                <span>姓名:</span>
    <el-input placeholder="请输入内容"></el-input>
    <span>年龄:</span>
    <el-input placeholder="请输入内容"></el-input>
    </span>
    <span>
                <span>性别:</span>
    <select name="sex">
                    <option value="男">男</option>
                    <option value="女">女</option>
                </select>
    </span>
    <span>
                <el-button type="primary" @click="addname">添加</el-button>
            </span>
  </form>
  <p>排序:
    <select name="sex" id="sortname">
                <option value="ById">ById</option>
                <option value="ByAge">ByAge</option>
            </select>
    <select name="sex" id="sortff">
                <option value="从小到大">从小到大</option>
                <option value="从大到小">从大到小</option>
            </select>
    <el-button type="primary" class="btn" @click="sort">排序</el-button>
    <el-button type="primary" class="btn" @click="deleteall">批量删除</el-button>
  </p>
  <div id="tablenew">
    <div id="tableul" class="clearfix">
      <ul>
        <li>全选
          <input type="checkbox" @click="quanxuan" id='qx' />
        </li>
        <li v-for="(item,index) in items">
          <input type="checkbox" class="dx" @click="danxuan(index)" :click='click' />
        </li>
      </ul>
      <ul>
        <li>ID</li>
        <li v-for="item in items">{{item.id}}</li>
      </ul>
      <ul>
        <li>姓名</li>
        <li v-for="item in items">{{item.name}}</li>
      </ul>
      <ul>
        <li>年龄</li>
        <li v-for="item in items">{{item.age}}</li>
      </ul>
      <ul>
        <li>性别</li>
        <li v-for="item in items">{{item.sex}}</li>
      </ul>
      <ul>
        <li>操作</li>
        <li v-for="(item,index) in items">
          <a href="javascript:;" @click="up(index)">上移</a> |
          <a href="javascript:;" @click="down(index)">下移</a> |
          <a href="javascript:;" @click="del(index)">删除</a>
        </li>
      </ul>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'table',
  data() {
    return {
      items: [{
          id: 5,
          name: '王小仙5',
          age: 36,
          sex: '男',
          isactive: false
        },
        {
          id: 4,
          name: '王小仙4',
          age: 43,
          sex: '男',
          isactive: false
        },
        {
          id: 3,
          name: '王小仙3',
          age: 28,
          sex: '男',
          isactive: false
        },
        {
          id: 2,
          name: '王小仙2',
          age: 25,
          sex: '女',
          isactive: false
        },
        {
          id: 1,
          name: '王小仙1',
          age: 22,
          sex: '女',
          isactive: false
        }
      ],
      num: 0,
      click: false,
      arr: [],
    }
  },
  mounted() {
    var exTable = document.getElementById('exTable');
    var inputs = exTable.getElementsByTagName('input');
    var sel = exTable.getElementsByTagName('select')[0];
    this.name = inputs[0];
    this.age = inputs[1];
    this.sel = sel;
    this.id = 5;
    this.sortname = document.getElementById('sortname')
    this.sortff = document.getElementById('sortff')
    this.qx = document.getElementById('qx')
    this.inputs = document.getElementsByClassName('dx')
    this.table = document.getElementById('tablenew');
  },
  methods: {
    addname() {
      if (this.name.value && this.age.value) {
        this.items.unshift({
          id: ++this.id,
          name: this.name.value,
          age: this.age.value,
          sex: this.sel.value,
          isactive: this.active
        })
        this.name.value = '';
        this.age.value = '';
      } else {
        alert('请输入内容')
      }
    },
    down(index) {
      let down = this.items.splice(index, 1);
      this.items.splice(index + 1, 0, down[0])
    },
    up(index) {
      if (index == 0) {
        return
      } else {
        let up = this.items.splice(index, 1);
        this.items.splice(index - 1, 0, up[0])
      }
    },
    del(index) {
      this.items.splice(index, 1)
    },
    sort() {
      if (this.sortname.value == 'ById' && this.sortff.value == '从小到大') {
        this.items.sort(function(a, b) {
          return a.id - b.id
        })
      }
      if (this.sortname.value == 'ById' && this.sortff.value == '从大到小') {
        this.items.sort(function(a, b) {
          return b.id - a.id
        })
      }
      if (this.sortname.value == 'ByAge' && this.sortff.value == '从小到大') {
        this.items.sort(function(a, b) {
          return a.age - b.age
        })
      }
      if (this.sortname.value == 'ByAge' && this.sortff.value == '从大到小') {
        this.items.sort(function(a, b) {
          return b.age - a.age
        })
      }
    },
    danxuan(index) {
      if (this.inputs[index].checked) {
        this.num++;
        if (this.num == this.items.length) {
          this.qx.checked = 'checked'
          this.click = true;
        }
      } else {
        this.num--;
        this.qx.checked = ''
        this.click = false;
      }

    },
    quanxuan() {
      if (!this.click) {
        this.click = true;
        this.num = this.items.length;
        for (var i = 0; i < this.inputs.length; i++) {
          this.inputs[i].checked = 'checked'
        }
      } else {
        this.click = false;
        this.num = 0;
        for (var i = 0; i < this.inputs.length; i++) {
          this.inputs[i].checked = ''
        }
      }
    },
    deleteall() {
      for (var i = 0; i < this.inputs.length; i++) {
        if (this.inputs[i].checked) {
          this.num--;
          this.inputs[i].checked = '';
          this.arr.push(this.items[i])
        }
      }
      for (var i = 0; i < this.items.length; i++) {
        for (var j = 0; j < this.arr.length; j++) {
          if (this.items[i] == this.arr[j]) {
            this.items.splice(i, 1)
          }
        }
      }
    }
  }
}
</script>

<style>
#exTable {
  height: 600px;
  min-width: 1080px;
  background: #e5e5e5;
  padding-top: 20px;
  border-radius: 10px;
}

#exTable a {
  color: cornflowerblue;
}

#exTable td {
  text-align: center;
}

#exTable .el-input {
  width: 200px;
}

#exTable ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

#exTable #form {
  text-align: center;
  margin-top: 40px;
}

#exTable p {
  text-align: center;
  margin-top: 40px;
}

#exTable #tableul {
  margin: 0 auto;
  width: 1080px;
  overflow: hidden;
  margin-top: 40px;
}

#exTable select {
  border-radius: 4px;
  background: #20a0ff;
  height: 36px;
  box-sizing: border-box;
  margin-right: 10px;
}

#exTable select option {
  height: 36px;
  line-height: 36px;
  background: #fff;
}

#exTable #tablenew ul {
  float: left;
  width: 180px;
  border-bottom: 1px solid #000;
}

#exTable #tablenew ul:last-of-type li {
  border-right: 1px solid #000;
}

#exTable #tablenew ul li {
  height: 30px;
  line-height: 30px;
  width: 180px;
  border: 1px solid #000;
  border-right: 0;
  border-bottom: 0;
  text-align: center;
  box-sizing: border-box;
}
</style>



